form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

input[type="text"] {
  padding: 12px 20px;
  margin-right: 10px;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

button[type="submit"] {
  padding: 12px 20px;
  background-color: #4caf50;
  color: white;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

ul {
  list-style-type: disc;
  margin: 0;
  padding: 0;
}

.todo-item {
  display: block;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}
